.search-form {
  background-color: #6ebeff;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 10px;
  display: flex;
}

.search-form input, .search-form button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: 10px;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  display: grid;
}

.photo-card {
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, .1);
}

.photo-card:hover, .photo-card:focus {
  box-shadow: 1px 1px 5px 1px rgba(0, 0, 0, .2);
}

.photo-card img {
  max-width: 100%;
  height: 280px;
  object-fit: cover;
  cursor: pointer;
  display: block;
}

.info {
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.button-container {
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
}

.load-more {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: 10px;
  display: none;
}

button:hover {
  background-color: #e6e6e6;
}

